<template>
    <wrap>

        <br>

        <!-- Group 1 -->

        <section class="light-blue lighten-2 py-12">
        <v-container>
        <v-card tile flat color="transparent">
        <v-row dense>
            <v-col cols="12" md="4">
                <v-card-title class="headline">
                    General financial data
                    </v-card-title>
                <v-card-subtitle class="caption">
                    Please group the fileds<br>
                    for a better readability
               </v-card-subtitle>
               <div class="">
                   <div class="overline pl-4 pb-1">Leading currency</div>

            <!-- ···························· Currencies -->
                <section v-for="list in [Currencies]" style="max-width:12em">
                    <div class="selectbox">
                        <v-select solo
                            v-model      = "list.uimodel"
                            :placeholder = "list.placeholder"
                            :items       = "list.GetItems()"
                            item-text    = "name"
                            item-value   = "rowid"
                            :success-messages = "!!list.uimodel && 'Wonderful!' ||''"
                            hide-details
                            />
                    </div>
                </section>
            <!-- ···························· -->

               </div>
            </v-col>
            <v-col cols="12" md="6">

            <!-- ···························· Fileds -->
                <v-row
                     v-for="indicator in [...Indicators].slice(0,4)"
                     :key="indicator.name"
                     class="align-center">
                    <v-col cols="5">
                        {{indicator.name}}
                    </v-col>
                    <v-col cols="4">
                        <v-text-field solo
                            v-model="indicator.index"
                            placeholder=""
                            type="number"
                            hide-details
                            />
                    </v-col>
                    <v-col cols="3">
                        <div class="headline">{{Currencies.GetItem(Currencies.uimodel).symbol}}</div>
                    </v-col>
                </v-row>
            <!-- ···························· -->

            </v-col>
        </v-row>
        </v-card>
        </v-container>
        </section>

        <!-- Group 2 -->

        <section class="light-blue accent-1 py-12">
        <v-container>
        <v-card tile flat color="transparent">
        <v-row dense>
            <v-col cols="12" md="4">
                <v-card-title class="headline">
                    Company details
                    </v-card-title>
                <v-card-subtitle class="caption">
                    Please group the fileds<br>
                    for a better readability
               </v-card-subtitle>
            </v-col>
            <v-col cols="12" md="6">

                <!-- ···························· Fileds -->

                <v-row
                     v-for="indicator in [...Indicators].slice(4)"
                     :key="indicator.name"
                     class="align-center">
                    <v-col cols="5">
                        {{indicator.name}}
                    </v-col>
                    <v-col cols="4">
                        <v-text-field solo
                            v-model="indicator.index"
                            placeholder=""
                            type="number"
                            hide-details
                            />
                    </v-col>
                </v-row>

                <!-- ···························· -->

            </v-col>
        </v-row>
        </v-card>
        </v-container>
        </section>


    <!-- ···························· DEV -->

    <v-container>
    <v-btn @click="Export(); forceRender()">Export data</v-btn>
    <debug :key="showInputData" v-show="Object.keys(inputData).length!=0">
        {{inputData}}
    </debug>
    </v-container>

    <!-- ···························· -->

    </wrap>
</template>

<script>
// ---------------------------- define export

    let props       = []
    let components  = {}
    let methods     = {}
    let computed    = {}
    let watch       = {}
    let data        = {}

// ---------------------------- main logic

    // use router
    computed = { ...computed, router ()   { return this.$router } }
    methods  = { ...methods,  goto (path) { return this.$router.push(path) } }

// ---------------------------- dev

    // create currency list

    import EditableList    from "@/models/EditableList"
    const Currencies = new EditableList({
        constructor: EditableList,
        uimodel: 1, // autoselect first option
    })

    const Indicators = [
        {name: "Revenue", index: null },
        {name: "Operating income", index: null },
        {name: "COGS: Cost of Goods Sold", index: null },
        {name: "Average annual inventory", index: null },
        {name: "Days Sales Outstanding", index: null },
        {name: "Open items - Overdue invoices from customers", index: null },
        {name: "Number of employees", index: null },
        {name: "DPO: Days Payable Outstanding", index: null },
        {name: "Open Items - Overdue Supplier Invoices", index: null },
        {name: "Additional KPI", index: null }
    ]

    data = { ...data, showInputData:0, inputData: new Object}

    data = {
        Currencies,
        Indicators,
    ...data }

    methods = {
        ExportAll () { return [
            Currencies.ExportList(),
        ]},
        Export(){
            let   RNA = this

            const currencyList = RNA.$data.Currencies
            let rowid = currencyList.uimodel
            let row = currencyList.GetItem(rowid)
            if(row)
            this.$data.inputData.LeadingCurrency = row.name

            const indicatorsList = RNA.$data.Indicators
            indicatorsList.map(indicator => {
                this.$data.inputData[indicator.name] = indicator.index ? indicator.index : 0
            })
        },

        Finish(){
            return true;
        },

        forceRender(){
            this.$data.showInputData += 1;
        },
    ...methods }

    // load data

    import ExampleList  from "@/models/ExampleList"

    Currencies.CreateList({...ExampleList['Currencies']})

// ---------------------------- ui actions

    data = {
        ui_editor: {
            state: false,
            list:  false,
            input: '',
        },
    ...data }

// ---------------------------- export ready

export default {
    name: 'part-M-0-Form2',
    props,
    components,
    methods,
    computed,
    watch,
    data () { return data },
}

// ---------------------------- end script
</script>
